md-toolbar.peach-toolbar {
  background-color: $toolbar-background;
  z-index: 80;

  .title-box {
    width: 180px;

    .title-row {

      .menu-wrapper {
        width: 60px;
        cursor: pointer;
        display: flex;
        justify-content: center;
        height: 64px;
        align-items: center;
        opacity: 0;

        .icon {
          color: $toolbar-font-color;
          font-size: 20px;
          font-size: 2rem;
        }
      }

      .name-wrapper {
        justify-content: center;
        align-items: flex-start;
        font-size: 16px;
        font-size: 1.6rem;

        img {
          width: 100%;
          opacity: 0;
          max-height: 40px;
        }

        span {
          opacity: 0;
        }
      }
    }
  }

  .apps-box {

    .apps-row {
      opacity: 0;

      .app-icon {
        background-color: $toolbar-icon-default-background;
        display: inline-block;
        cursor: pointer;
        margin-left: 20px;
        width: 40px;
        height: 40px;
        line-height: 40px;
        vertical-align: middle;
        text-align: center;

        &:last-child {
          padding-right: 0px;
        }

        a {
          display: block;
          height: 40px;

          .icon {
            background-size: 100%;
            color: $toolbar-font-color;
            font-size: 30px;
            font-size: 3rem;
            vertical-align: middle;
            display: block;
            width: 40px;
            height: 40px;

            &:hover {
              color: $inverse-font-color;
            }
          }

          p {
            display: none;
          }

          &.all-apps-icon {
            font-size: 12px;
            font-size: 1.2rem;
            color: $inverse-font-color;
          }
        }
      }

      .apps-container-row {
        align-items: center;
        line-height: 64px;
      }

      .apps-column-left {

        .apps-container-row {
          white-space: nowrap;
          justify-content: flex-start;

          .all-apps-icon {
            background-color: transparent;
            color: $inverse-font-color;
            width: 40px;
            font-size: 9px;
            font-size: 0.9rem;
            text-align: center;
            text-transform: uppercase;
            line-height: 10px;
            padding-top: 5px;
            padding-bottom: 5px;

            p {
              margin: 0 0 5px 0;
            }

            span {
              display: block;
              text-align: center;
            }
          }
        }
      }

      .apps-column-right {

        .apps-container-row {
          justify-content: flex-end;

          .app-icon {
            background-color: transparent;

            &.active {
              a {
                .icon {
                  color: $toolbar-active-right-font-color;
                }
              }
            }
          }
        }
      }
    }
  }

  .menu-box {
    padding: 0 20px;
    cursor: pointer;

    .menu-header {
      justify-content: space-between;
      align-items: center;
      font-weight: 600;
      font-size: 14px;
      font-size: 1.4rem;
      max-width: 190px;

      .img-wrapper {
        width: 33px;
        padding-top: 2px;
        position: relative;

        img {
          width: 100%;
          border-radius: 33px;
          opacity: 0;
        }

        md-icon.icon-user {
          background: none;
          height: 33px;
          width: 33px;
          font-size: 33px;
          font-size: 3.3rem;
          margin-top: -2px;
        }

        .alert-bubble {
          width: 20px;
          height: 20px;
          position: absolute;
          background: $toolbar-alert-bubble-background;
          border-radius: 20px;
          z-index: 1;
          top: -5px;
          right: -5px;
          text-align: center;
          color: $inverse-font-color;
          line-height: 20px;
          font-size: 10px;
        }
      }

      .name-wrapper {
        color: $toolbar-user-font-color;
        padding-left: 20px;
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;

        span {
          opacity: 0;
        }
      }

      .toggle-wrapper {
        color: $toolbar-user-font-color;

        .icon-caret {
          font-size: 9px;
          font-size: 0.9rem;
          padding-left: 5px;
          opacity: 0;
        }
      }
    }
  }

  .mobile-app-box {

    .mobile-app-row {
      justify-content: center;
    }
  }

  /* Mobile */
  @media (max-width: 767px) {
    background-color: $mobile-toolbar-background;

    .title-box {
      width: 64px;

      .name-wrapper {
        display: none;
      }
    }

    .apps-container-row {
      display: none;
    }
  }

  md-toolbar {
    background-color: $mobile-toolbar-location-picker-background;
    align-items: center;
    min-height: 36px;
    max-height: 36px;

    .icon-location {
      padding: 4px 5px 0 20px;
      font-size: 24px;
      font-size: 2.4rem;
    }

    peach-select md-input-container.peach-select-input input.selection-label {
      color: $toolbar-font-color;
      font-size: 1.6rem;
    }
  }
}



