peach-accordion {

  peach-accordion-panel {

    peach-accordion-header {

      span {

        &.icon {
          width: 32px;
          height: 32px;
          background-size: 32px 32px;
          display: inline-block;
          vertical-align: middle;

          + h4 {
            font-weight: 600;
            padding-left: 10px;
            display: inline-block;
          }
        }
      }
    }

    &:not(.is-open) {

      peach-accordion-body {
        display: none;
      }
    }
  }
}



