md-menu-content#peach-select-menu-content {
  overflow-x: hidden;

  &.search-enabled {
    min-height: 120px;
  }

  &.multi-select:not(.search-enabled) {

    md-menu-item.peach-select-option {
      padding-left: 34px;

      &:before {
        transition: 240ms;
        position: absolute;
        left: 16px;
        width: 20px;
        height: 20px;
        content: ' ';
        background-color: $checkbox-background;
        margin-top: 13px;
        z-index: 1;
      }

      &.selected {

        &:before {
          background-color: $checkbox-background-active;
        }

        &:after {
          transform: rotate(45deg);
          position: absolute;
          top: 14px;
          left: 22px;
          display: table;
          width: 6px;
          height: 12px;
          border: 2px solid;
          border-top: 0;
          border-left: 0;
          content: ' ';
          z-index: 2;
          border-color: rgb(255, 255, 255);
        }
      }
    }
  }

  md-menu-item {
    position: relative;

    &[disabled] {
      color: $font-color-disabled;
    }

    &.peach-select-search {

      .icon-x {
        cursor: pointer;
        position: absolute;
        top: 7px;
        right: 20px;
        z-index: 2;
      }

      &:not(.md-input-has-value) {
        font-style: italic;

        label {
          margin-left: 16px;
          font-weight: normal;
          transform: none;
          top: 9px;
        }
      }

      md-input-container.md-input-has-value,
      md-input-container.md-input-focused {

        label {
          display: none;
        }
      }
    }

    &.peach-select-option:not([disabled]) {
      &:not(.no-results):hover {
        cursor: pointer;
        background: rgb(238,238,238);
      }

      &.no-results {
        font-size: 14px;
        font-size: 1.4rem;
        color: $font-color-disabled;
      }
    }
  }
}



