peach-modal-content {
  display: flex;
  flex: 1;
  flex-direction: column;
  background-color: $modal-background;
  height: calc(100% - 51px);
  overflow-y: auto;

  > md-content {
    background-color: $modal-background;
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    flex-shrink: 0;

    &.can-load::after {
      content:'';
      display: block;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      background-color: rgba(255, 255, 255, 1);
      z-index: 1;
    }

    &.can-load.fade-in::after {
      animation: fadeOutWhiteOverlay both 0.5s;
      display: none;
    }

    > peach-modal-header ~ *:not(md-tabs) {
      padding-left: 30px;
      padding-right: 30px;
    }

    peach-card {

      md-card {
        margin-left: 0;
        margin-right: 0;
      }
    }
  }

  md-content,
  md-content.md-default-theme {
    background-color: $modal-background;

    &.can-load {
      opacity: 0;
    }

    .md-padding {

      &.peach-no-padding-top {
        padding-top: 0;
      }
    }
  }

  md-tabs {
    margin-top: -18px;

    md-tabs-wrapper md-tabs-canvas md-pagination-wrapper md-tab-item:not([disabled]).md-active,
    md-tabs-wrapper md-tabs-canvas md-pagination-wrapper md-tab-item:not([disabled]).md-focused {
      color: $inverse-font-color;
    }

    md-tabs-wrapper md-tabs-canvas md-pagination-wrapper md-tab-item:not([disabled]) {
      color: rgba(255, 255, 255, .5);
    }

    md-tabs-wrapper md-tabs-canvas md-pagination-wrapper .md-tab .md-ripple-container {
      color: rgba(255, 255, 255, .5);
    }

    md-tabs-wrapper md-tabs-canvas md-pagination-wrapper md-ink-bar {
      color: $modal-background;
      background-color: $modal-background;
    }
  }

  /* Tablet+ */
  @media (min-width: 768px) {
    .peach-modal-content {
      padding-top: 30px;
      padding-bottom: 80px;
    }
  }

  /* Mobile */
  @media (max-width: 767px) {
    .peach-modal-content {
      padding-top: 20px;
      padding-bottom: 50px;

      > peach-modal-header ~ *:not(md-tabs) {
        padding-left: 20px;
        padding-right: 20px;
      }
    }
  }
}



