md-sidenav.peach-usermenu {

  .alerts-container {

    h4 {
      color: $font-color-disabled;
      font-size: 14px;
      font-size: 1.4rem;
      font-weight: 400;
      margin: 0;
      padding: 20px 30px 0;
    }

    md-list {
      padding: 20px 0 0 30px;

      md-list-item {
        padding: 0 0 20px;

        a {
          font-size: 16px;
          font-size: 1.6rem;
          font-weight: 600;
        }

        img {
          width: 45px;
          height: 45px;
          box-sizing: content-box;
          margin: 0 20px 0 0;
          float: left;
        }

        small {
          font-size: 12px;
          font-size: 1.2rem;
        }

        .alert-content {
          display: inline-block;
          width: 60%;
        }
      }
    }
  }

  .icon-x {
    color: $font-color-disabled;
    height: auto;
    font-size: 16px;
    font-size: 1.6rem;

    &:before {
      font-weight: 600 !important;
    }
  }
}

/* Mobile */
@media (max-width: 767px) {
  md-sidenav.peach-usermenu {
    background-color: $mobile-sidenav-background;
    z-index: 80;

    > md-content {
      background-color: $mobile-sidenav-background;
    }

    h3 {
      font-size: 18px;
      font-size: 1.8rem;
      font-weight: 600;
      display: inline-block;
      padding: 0 16px;

      a {
        color: $mobile-sidenav-link-font-color;
        text-decoration: none;
      }
    }

    md-list-item {
      padding: 0;
    }

    peach-accordion {
      width: 100%;

      peach-accordion-header {

        div {

          h3 {
            cursor: pointer;
            color: $mobile-sidenav-header-color;

            a {
              color: inherit;
              text-decoration: none;
            }
          }
        }
      }

      peach-accordion-body  {

        div {
          background-color: $mobile-sidenav-accordion-background;

          md-list {

            md-list-item {
              min-height: 43px;

              a {
                color: $mobile-sidenav-link-font-color;
                cursor: pointer;
                font-weight: 400;
                text-decoration: none;
              }

              &.active a {
                color: $mobile-sidenav-active-link-font-color;
                font-weight: 600;
              }

              .md-list-item-inner {
                background-color: transparent;
                z-index: 2;
                position: relative;
              }

              button.md-button {
                padding: 0 36px;

                &:not([disabled]):hover {
                  background-color: transparent;

                  a {
                    color: $mobile-sidenav-active-link-font-color;
                  }
                }
              }
            }
          }
        }
      }
    }
  }

  .alerts-container {

    small {
      color: $font-color-disabled;
    }
  }
}

/* Tablet+ */
@media (min-width: 768px) {
  md-sidenav.peach-usermenu {
    background-color: $body-background;
    overflow-y: auto;
    top: 0;
    margin-left: -5px;
    bottom: inherit;
    z-index: 70;
    max-height: 80%;
    max-height: 100%;

    > md-content {
      background-color: $body-background;
    }

    &.md-closed {
      transform: translate3d(-100%, -100%, 0);
      z-index: 1;
      opacity: 0;
    }

    md-list {
      padding: 30px 30px 0;
      box-sizing: content-box;

      md-list-item {
        min-height: 20px !important;
        padding: 0 0 30px;
        margin: 0;
        box-sizing: content-box;

        .md-list-item-inner {
          min-height: 20px !important;
        }

        .md-list-menu-text {
          padding: 0;

          h3 {
            margin: 0;
            font-weight: 600;

            a {
              color: $font-color;
              text-decoration: none;
              transition: $font-color-hover-animation;
              font-size: 16px;
              font-size: 1.6rem;
              font-weight: normal;

              &:hover {
                color: $font-color-hover;
              }
            }
          }
        }
      }
    }
  }
}



