md-sidenav.peach-all-apps-menu {
  background-color: #333;
  top: 64px;
  bottom: inherit;
  z-index: 70;
  left: 100%;
  margin-left: -190px;
  flex-wrap: wrap;
  padding: 20px 10px 0;
  width: 240px;
  min-width: 240px;
  max-width: 240px;

  &.md-closed {
    transform: translate3d(-100%, -100%, 0);
    z-index: 1;
    opacity: 0;
  }

  .app-icon {
    font-size: 9px;
    font-size: 0.9rem;
    word-wrap: normal;
    width: 55px;
    text-align: center;
    text-transform: uppercase;
    cursor: pointer;

    &.active {
      a {
        .icon {
          color: $inverse-font-color !important;
        }
      }
    }

    a {
      .icon {
        color: $toolbar-font-color !important;
      }
    }

    span {
      background-size: 100%;
      display: block;
      width: 35px;
      height: 35px;
      font-size: 30px;
      font-size: 3rem;
      margin: 0 auto;
    }

    p {
      color: $toolbar-font-color;
      margin-top: 10px;
      margin-bottom: 20px;
      display: block;
    }
  }
}



