    margin: 30px 0 !important; }
  peach-modal md-content.peach-modal .modal-body {
    padding: 0; }
  peach-sidenav.peach-modal-active + #content.sidenav-open [ng-view] {
    margin-left: 0 !important; }
    peach-sidenav.peach-modal-active + #content.sidenav-open [ng-view].peach-modal {
      margin-left: 0 !important; }
  html, body {
    overflow: initial;
    height: auto; }
