peach-message {

  .message-wrapper {
    display: flex;
    flex-direction: row;
    font-size: 1.4rem;
    padding: 20px;
    min-height: 24px;
    color: $inverse-font-color;
    background-color: #000;
    margin: 30px;
    margin-bottom: 0;

    .message-icon-status {
      font-size: 2.0rem;
      line-height: 2.0rem;
      padding-right: 20px;
    }

    .message-dismiss {
      padding-left: 20px;
    }
  }

  &.alert .message-wrapper {
    background-color: #F3503A;
  }

  &.success .message-wrapper {
    background-color: #00BD9C;
  }

  &.info .message-wrapper {
    background-color: #33AEDC;
  }

  &.warning .message-wrapper {
    background-color: #F6C342;
  }
}

peach-modal-content {

  peach-message {

    .message-wrapper {
      margin-left: 0;
      margin-right: 0;
    }
  }
}



