peach-modal-header, *[peach-modal-header] {
  display: flex;
  height: auto;
  min-height: 100px;
  border: none;
  opacity: 1;
  margin: -30px 0 0;
  padding-right: 30px;

  md-toolbar:not(.md-menu-toolbar) {
    background-color: transparent;
    height: auto;
    min-height: 100px;

    &.can-load {
      opacity: 0;
    }

    [ng-transclude]:not(.md-button) {
      height: 100px;
      align-items: center;

      & > img {
        border-radius: 50%;
        margin-right: 15px;
        width: 50px;
      }
    }
  }

  .peach-modal-header {
    padding: 0 10px 0 30px;
    color: $inverse-font-color;

    a {
      color: $inverse-font-color;
      font-weight: 600;
      text-decoration: none;
    }

    h1 {
      font-size: 26px;
      font-size: 2.6rem;
      font-weight: 100;
      margin: 0;
      padding: 0;
      padding-right: 20px;
    }

    md-input-container {
      margin-top: 30px;

      .md-input {
        border-color: $inverse-font-color;
      }

      &:not(.md-input-invalid) {

        &.md-input-focused .md-input {
          border-color: $inverse-font-color;
        }

        &.md-input-has-value {

          label {
            color: $inverse-font-color;
          }
        }
      }

      &.md-input-invalid {

        &.md-input-focused label {
          color: $inverse-font-color;
        }

        label {
          color: $inverse-font-color;
        }
      }

      input, label {
        color: $inverse-font-color;
      }
    }

    button.close-button {
      height: 48px;
      width: 48px;
      margin-right: -18px;

      .icon-x {
        font-size: 16px;
        font-size: 1.6rem;

        &:before {
          font-weight: 600 !important;
        }
      }
    }

    md-checkbox:not([disabled]).md-checked {

      .md-container {

        .md-icon {
          background-color: rgba(255, 255, 255, 0.25);
        }
      }

    }

    md-icon[class^='icon'] {
      color: $inverse-font-color;
      height: auto;
    }

    md-progress-linear {

      .md-container {
        background-color: rgba(255, 255, 255, 0.25);
        height: 20px;

        .md-bar {
          background-color: $modal-loading-linear-background;
          height: 20px;
        }
      }
    }

    .md-button.md-primary {
      background-color: rgba(255, 255, 255, 0.25);
      max-height: 36px;
    }
  }

  & + form {
    margin-top: 30px;
  }
}

/* Mobile */
@media (max-width: 767px) {
  peach-modal-header, *[peach-modal-header] {
    height: auto;
    min-height: 50px;
    margin: -20px 0 0 -20px;
    padding-left: 20px;
    padding-right: 0;

    md-toolbar:not(.md-menu-toolbar) {
      height: auto;
      min-height: 50px;

      [ng-transclude]:not(.md-button) {
        display: block;
        height: auto;

        & > img {
          display: none;
        }
      }
    }

    .peach-modal-header {
      padding: 0 0 0 20px;

      h1 {
        font-size: 18px;
        font-size: 1.8rem;
      }

      md-input-container {
        margin-top: 25px;
        margin-bottom: 10px;
        width: 200px;
        min-width: 200px;

        &:not(.md-input-invalid).md-input-has-value {

          label {
            font-size: 18px;
            font-size: 1.8rem;
          }
        }

        input, label {
          font-size: 18px;
          font-size: 1.8rem;
        }
      }

      button.close-button {
        margin-right: 0px;
      }
    }
  }
}



