peach-card-header {
  display: flex;
  flex-direction: row;
  padding: 15px 20px;
  border-bottom: $card-header-border;
  max-height: 50px;

  h4 {
    color: #58595b;
    font-weight: 600;
    margin: 0;
    line-height: 18px;

    &.can-load {
      opacity: 0;
    }
  }

  .collapsible.icon-caret {
    display: flex;
    flex-grow: 1;
    justify-content: flex-end;
    color: $font-color;
    font-size: 13px;
    font-size: 1.3rem;
    height: 18px;
    line-height: 20px;

    & :not(.is-open) {

      &:before {
        display: inline-block;
        transform: rotate(180deg);
      }
    }
  }

  & > [ng-transclude] {
    display: flex;
    flex-grow: 1;
    position: relative;

    &.can-load {
      opacity: 0;
    }

    .md-button {
      margin-top: -15px;
      margin-bottom: -15px;

      span {
        color: $button-background-primary;
      }
    }

    .card-toggle-button {
      margin-right: -20px;

       .icon {
         height: auto;
       }
    }

    /* Mobile */
    @media (max-width: 767px) {
      flex-wrap: wrap;
      align-items: flex-start;

      .md-button {
        margin-top: -7px;
        margin-bottom: -7px;
      }
    }
  }

  div {
    div {
      span {
        color: #58595b;
        font-weight: 600;
        line-height: 18px;
      }
    }
  }
}



