:focus {
  outline: none;
}

md-content.md-default-theme,
md-content,
#page_frame,
[ng-view] {
  background-color: $body-background;
}

html, header, body, input, select, textarea {
  font-family: 'ProximaNova', Arial;
  color: $font-color;
}

html { font-size: 62.5%; }
body {
  font-size: 1.6rem;

  & > .peach-page {
    height: 100%;

    & > .peach-page-wrapper {
      height: 100%;

      & > .peach-page-content-row {
        overflow: hidden;
      }
    }
  }
}

html, body {
  overflow: hidden;
  -webkit-overflow-scroll: auto;
}

#content {
  width: 100%;
  position: relative;
}

#page_frame {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
}



