md-switch {

  &:not([disabled]) {

    .md-bar {
      background-color: $switch-bar-background;
    }

    .md-thumb {
      background-color: $switch-thumb-background;

      .md-ripple {
        background-color: $switch-thumb-background !important;
        border-color: $switch-thumb-background !important;
      }
    }

    &.md-checked {

      .md-bar {
        background-color: $switch-bar-background;
      }

      .md-thumb {
        background-color: $button-background-primary;

        .md-ripple {
          background-color: $button-background-primary !important;
          border-color: $button-background-primary !important;
        }
      }
    }
  }

  &[disabled] {

    .md-bar {
      background-color: $switch-bar-disabled-background;
    }

    &.md-checked {

      .md-bar {
        background-color: $switch-bar-disabled-background;

      }

      .md-thumb {
        background-color: $button-background-primary;
      }
    }

    .md-thumb {
      background-color: $switch-thumb-background;
    }
  }
}



