.peach-alert {
  position: absolute;
  top: 64px;
  left: 180px;
  right: 0;
  bottom: 0;
  z-index: 9999;
  overflow-y: auto;
  overflow-x: hidden;
  padding: 0;
  margin-left: 100%;
  transition: margin-left 0.25s;
  box-shadow: -1px 0px 10px -3px $modal-shadow;
  border-radius: 0;
  max-width: 100%;
  max-height: 100%;

  &:not(.ng-hide) {
    margin-left: 0;
  }

  &.full-width {
    left: 0;
  }

  .md-button {
    &:last-child {
      margin-right: -10px;
    }
  }

  .md-virtual-repeat-container {

    .repeated-item {
      margin-top: 30px;
    }
  }

  .icon-x {
    color: $font-color-disabled;
    height: auto;
    font-size: 16px;
    font-size: 1.6rem;

    &:before {
      font-weight: 600 !important;
    }
  }

  peach-page-header, [peach-page-header] {
    border: none;

    .peach-page-header {
      background-color: $content-frame-background;

      & > img {
        border-radius: 50%;
        margin-top: 30px;
        margin-right: 30px;
        width: 50px;
      }
    }
  }

  md-dialog-content {
    background-color: $content-frame-background !important;
    overflow: hidden;
    padding: 30px 0 0 0;

    .buttons {
      margin-right: 40px;
    }

    md-tabs {
      border-top: 1px solid $table-row-border;
      height: inherit;
      position: static;

      md-ink-bar {
        color: $inverse-font-color;
        background-color: transparent;
      }

      md-tabs-wrapper {
        background-color: $inverse-font-color;
        padding: 0;
      }

      md-tab-content {
        padding: 0 0 30px 30px;
        overflow: hidden;

        .message-wrapper {
          margin: 30px 30px 30px 0;
        }
      }

      .md-tab {
        background-color: $tab-background;
        border-right: 1px solid $table-row-border;
        color: $font-link-color;
        font-size: 16px;
        font-size: 1.6rem;
        font-weight: 600;
        padding-left: 30px;
        padding-right: 30px;

        &.md-active {
          background-color: transparent;
          color: $font-color;
        }

        .md-ripple-container {
          color: $font-link-color;
        }
      }
    }

    md-list {

      md-list-item {

        &:first-child {

          .md-list-item-text {
            padding-top: 0;
          }
        }

        a {
          font-weight: 600;
        }

        div {
          line-height: 24px;
          line-height: 2.4rem;

          .alert-content {
            display: inline-block;
            width: 85%;
          }
        }

        img {
          width: 45px;
          height: 45px;
          box-sizing: content-box;
          margin: 0 20px 40px 0;
          float: left;
        }

        small {
          color: $font-color-disabled;
          display: block;
        }

        .md-button {
          margin: -3px 0;
          font-weight: 600;

          &:last-child {
            margin-right: 20px;
          }
        }
      }
    }
  }

  /* Mobile */
  @media (max-width: 767px) {
    left: 0;
    box-shadow: 0;
  }

  @media (max-width: 600px) {
    md-dialog-content {
      md-list {
        md-list-item {
          div {
            .alert-content {
              width: 65%;
            }
          }
        }
      }
    }
  }
}



