md-progress-linear {
  margin-top: -3px;
  height: 3px;

  .md-container {
    background-color: $loading-linear-background;
    height: 3px;

    .md-bar {
      background-color: $loading-linear-foreground;
      height: 3px;
    }
  }

  &.ng-leave {
    animation: fadeOut both 0.5s;
  }
}



