/* This file will never contain any styles
    besides the overrides needed to configure a theme */

$theme-color-primary: #BAB1AA;

md-toolbar.peach-toolbar .apps-box .apps-row .app-icon.active a .icon {
  color: $theme-color-primary;
}

md-toolbar.peach-toolbar .mobile-app-box .app-icon.active a .icon {
  color: $theme-color-primary;
}

md-toolbar.peach-toolbar .mobile-app-box .app-icon.active a p {
  color: $theme-color-primary;
}

peach-modal-header, *[peach-modal-header] {
  background-color: $theme-color-primary;
}

peach-modal-content md-tabs md-tabs-wrapper, *[peach-modal-content] md-tabs md-tabs-wrapper {
  background-color: $theme-color-primary;
}



