md-tabs {

  md-tabs-wrapper {

    md-tabs-canvas {
      height: 50px;

      md-pagination-wrapper {
        height: 50px;

        md-tab-item {
          height: 50px;

          &:not([disabled]) {
            color: $font-link-color;

            &.md-active,
            &.md-focused {
              color: $font-color;
            }
          }
        }

        md-ink-bar {
          color: $font-link-color;
          background-color: $font-link-color;
        }

        .md-tab {

          .md-ripple-container {
            color: $font-link-color;
          }
        }
      }
    }
  }

  md-tabs-content-wrapper {
    top: 50px;
  }
}



