.md-button {

  border-radius: 0;
  font-weight: 600;

  &.md-primary {
    color: $button-background-primary;

    &.md-raised,
    &.md-fab {

      &:not([disabled]) {
        background-color: $button-background-primary;
        box-shadow: none;
        color: $inverse-font-color;

        &:hover {
          background-color: $button-background-primary;
        }
      }
    }
  }

  &.md-raised {
    &:not([disabled]) {
      box-shadow: none;
    }
  }

  &.has-progress {
    max-height: 36px;
  }

  md-progress-circular {

    &[md-diameter="16"] {
      margin-left: auto;
      margin-right: auto;
    }

    .md-inner  {
      .md-gap {
        border-top-color: $inverse-font-color;
        border-bottom-color: $inverse-font-color;
      }

      .md-left {
        .md-half-circle {
          border-top-color: $inverse-font-color;
          border-left-color: $inverse-font-color;
        }
      }

      .md-right {
        .md-half-circle {
          border-top-color: $inverse-font-color;
          border-right-color: $inverse-font-color;
        }
      }
    }
  }
}



