.md-chips {

  .md-chip {
    background-color: $chip-background;
    border-radius: 0;
    color: $font-color;
    margin: 10px;
    height: 25px;
    line-height: 25px;

    span {

      &.tag-selected {
        color: $chip-selected-color;
      }

      strong {
        font-weight: 400;
      }
    }

    .md-chip-remove-container {
      height: 25px;
      width: 30px;

      button.md-chip-remove,
      button.md-button.md-icon-button {
        height: 25px;
        width: 30px;
        padding: 0;
        margin: 0;
        line-height: 25px;
        min-height: 25px;

        md-icon {
          vertical-align: initial;
        }
      }
    }
  }

  &:not(.md-readonly) {

    .md-chip {
      &:not(.md-readonly) {
        padding-right: 30px;
      }
    }
  }
}



