.peach-app-launcher {

  /* Mobile */

  &.active {
    @media (max-width: 767px) {

      a {
        .icon {
          vertical-align: middle;
          display: inline-block;
          font-size: 25px;

          &:not([class*=" icon-"]) {
            width: 32px;
            height: 32px;
            background-size: 32px 32px;
            padding-left: 32px;
            margin-left: -32px;
            margin-right: 8px;
          }
        }

        p {
          display: inline-block;
          padding-left: 0;
        }
      }
    }
  }
}



