ul {

  &.peach-modal-nav-list {
    list-style: none;
    padding-left: 0;

    li {
      text-transform: uppercase;
      font-weight: bold;
      color: #2D97CC;

      &.active {
        color: #A7A9AC;
      }

      &:not(.active):hover {
        color: #34B1EF;
        cursor: pointer;
      }
    }
  }
}



