peach-modal {
  position: absolute;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;

  > md-progress-linear {
    margin-top: 0;
    position: absolute;
    z-index: 1;
  }

  > md-content.md-default-theme,
  > md-content {
    height: 100%;
    background-color: $modal-background;
  }
}



