.peach-search {
  margin-right: -20px;

  .icon-search {
    color: $font-color-disabled;
    font-size: 18px;
    font-size: 1.8rem;
  }

  .icon-x {
    color: $font-color-disabled;
    font-size: 16px;
    font-size: 1.6rem;

    &:before {
      font-weight: 600 !important;
    }
  }

  md-sidenav {
    .md-button {
      &.md-icon-button {
        margin: 0;
        margin-right: 20px;
        top: 15px;
      }
    }
  }

  .md-sidenav-backdrop {
    background-color: transparent;
    transition: none;
  }

  .md-sidenav-right {
    background-color: $body-background;
    justify-content: flex-end;
  }

  md-input-container {
    font-size: 16px;
    font-size: 1.6rem;

    .icon-search {
      font-size: 18px;
      font-size: 1.8rem;
      left: 10px;
    }
  }
}



