md-whiteframe.peach-content-frame {
  margin: 30px;
  box-shadow: 0px 3px 5px -1px rgba(0, 0, 0, 0.1),
              0px 6px 10px 0px rgba(0, 0, 0, 0.04),
              0px 1px 9px 0px rgba(0, 0, 0, 0.06);

  md-content.peach-content {
    background-color: $content-frame-background;

    .peach-content-header {
      padding: 14px 20px 15px;
      border-bottom: 1px solid #E5E5E5;
      justify-content: space-between;
      align-items: center;

      h3 {
        margin: 0;
        font-size: 16px;
        font-size: 1.6rem;
        font-weight: 600;
      }

      .header-update-time {
        font-size: 14px;
        font-size: 1.4rem;
      }
    }

    .peach-content-body {
      padding: 20px;

      .peach-content-gutter {
        width: 180px;
        padding: 0 20px 0 0;

        img {
          width: 100%;
        }
      }

      .peach-content-primary {

        form {

          .button-row {
            justify-content: flex-end;
          }
        }
      }
    }

    .peach-content-table {

    }
  }
}



