md-select-menu {

  md-content {
    background-color: $content-frame-background;
  }

  &.md-default-theme {

    md-option[selected] {
      color: $font-color;
      font-weight: bold;
    }
  }

  &[multiple] {

    md-option {
      padding-left: 48px;

      &:before {
        transition: 240ms;
        position: absolute;
        top: 11px;
        left: 16px;
        width: 22px;
        height: 22px;
        border: 2px solid;
        border-radius: 2px;
        content: ' ';
        border: none;
        z-index: 1;
        background-color: $checkbox-background;
      }

      &[selected] {

        &:before {
          background-color: $checkbox-background-active;
        }

        &:after {
          -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
          position: absolute;
          top: 15px;
          left: 24px;
          display: table;
          width: 3px;
          height: 9px;
          border: none;
          content: ' ';
          z-index: 2;
          border: 3px solid;
          border-top: 0;
          border-left: 0;
          border-color: rgb(255, 255, 255);
        }
      }
    }
  }
}



