form {

  md-input-container {
    .md-input {
      border-bottom-color: $form-input-inactive-bottom-border;
      color: $font-color;
    }
  }

  & + peach-card {

    & md-card {
      margin-top: 12px;
    }
  }

  .button-container {
    align-items: flex-end;
  }
}

md-input-container {

  &:not(.md-input-focused):not(.md-input-has-value):not(.md-input-has-placeholder):not(.md-input-invalid) {

    label:not(.md-no-float):not(.md-container-ignore),
    .md-placeholder,
    .md-select-placeholder span {
      color: $font-color;
      font-style: italic;
      font-weight: 100;
    }
  }

  &:not(.md-input-invalid).md-input-has-value {
    label {
      color: $font-color-disabled;
    }
  }

  &:not(.md-input-invalid).md-input-focused {

    .md-input {
      border-color: $font-link-color;
    }

    label {
      color: $font-color-disabled;
    }
  }

  &.md-input-invalid {

    &.md-input-focused {

      label {
        color: $font-color-disabled;
      }

      .md-placeholder {
        opacity: 0;
      }
    }

    label:not(.md-no-float):not(.md-container-ignore) {
      transform: translate3d(0,6px,0) scale(0.75);
    }

    &[ng-message] {
      color: $textbox-border-invalid;
    }

    .md-input {
      border-bottom-width: 2px;
      border-bottom-color: $textbox-border-invalid;
    }

    md-select {

      .md-select-value {
        border-bottom-width: 2px;
        border-bottom-color: $textbox-border-invalid;
      }
    }
  }

  &.md-input-focused {

    .hint {
      color: $font-color-disabled;
      font-size: 12px;
      font-size: 1.2rem;
      display: block;
    }

    [ng-messages] {
      display: none;
    }

    .md-placeholder {
      opacity: 0;
    }
  }

  &.md-input-has-value {
    label {
      padding-top: 4px;
    }
  }

  .hint {
    display: none;
  }

  [ng-messages] {
    display: block;
  }

  .md-input {

    &[disabled] {
      background: none;
      color: $font-color;
      -webkit-text-fill-color: $font-color;
      -webkit-opacity: 1; 
    }

    &.ng-invalid {
      border-bottom-width: 2px;
      border-bottom-color: $textbox-border-invalid;
    }
  }
}



@media (min-width: 960px) {
  md-card, peach-modal-content {

    *:not(peach-modal-header) {
      md-input-container {

        &:not(:last-child) {
          padding-right: 20px;
        }
      }
    }
  }

  peach-modal-content {
    *:not(peach-modal-header) {
      md-input-container {

        &.flex-50:first-child {
          padding-right: 10px;
        }
      }
    }
  }
}

.peach-chips-container {
  display: inline-block;
  position: relative;
  padding: 2px;
  margin: 18px 0;
}



