md-input-container.md-default-theme {

  .md-errors-spacer {
    display: none;
  }

  label {
    color: $font-color;
  }

  .md-input {
    color: $font-color;
    border-width: 0 0 2px 0;
  }

  &.md-input-invalid {
    .md-input {
      border-color: $textbox-border;
      padding-bottom: 1px;
    }
  }

  &.md-input-focused {
    .md-input {
      padding-bottom: 1px;
    }
  }

  &.md-input-focused:not(.md-input-invalid) {
    label {
      color: $font-color-disabled;
    }
  }

  &.md-input-has-value {
    label {
      color: $font-color-disabled;
    }

    .md-input {
      padding-bottom: 1px;
    }
  }

  &.md-input-has-value.md-input-invalid:not(.md-input-focused) {
    .md-input {
      border-color: $textbox-border-invalid;
      padding-bottom: 1px;
    }
  }

  .md-button {
    height: 34px;
    color: $inverse-font-color;
    transition: $button-background-hover-animation;

    &.md-primary {

      &.md-raised {
        background-color: $button-background-primary;
        box-shadow: none;
        border-radius: 0;
        margin-top: 4px;

        &:not([disabled]) {

          &:hover,
          &:focus {
            background-color: $button-background-primary-active;
            transform: none;
          }
        }
      }
    }
  }
}

md-input-container {

  .md-errors-spacer {
    display: none;
  }

  .md-input {
    color: $font-color;
  }
}



