/* Fonts */
.font-default {
  color: $font-color;
}

a.font-default {
  color: $font-color;
  transition: $font-color-hover-animation;

  &:hover {
    color: $font-color-hover;
  }
}

.font-inverse {
  color: $inverse-font-color;
}

.font-disabled {
  color: $font-color-disabled;
}

.font-error {
  color: $font-color-error;
}

.font-success {
  color: $font-color-success;
}


/* Backgrounds */

.bg-default {
  background-color: $body-background;
}



