peach-page-header-options, *[peach-page-header-options] {
  -webkit-justify-content: flex-start;
  justify-content: flex-start;
  display: flex;
  flex: 1;

  /* Mobile */
  @media (max-width: 767px) {
    -webkit-justify-content: flex-end;
    justify-content: flex-end;
  }

  .peach-page-header-options {

    .md-menu {
      position: relative;

      &:after {
        border-left: $page-header-border;
        display: block;
        height: 35px;
        content: '';
        position: absolute;
        top: 0px;
        right: 0px;
      }
    }

    .md-button:before {
      border-left: $page-header-border;
      display: block;
      height: 50px;
      content: '';
      position: absolute;
      margin-left: -20px;
      top: -6px;

    }

    .icon {
      color: #A7A9AC;
      font-size: 1.8rem;
      height: auto;

      &.icon-vert-ellipsis {
        color: $nav-link-color;
      }

    }

  }
}



