md-dialog.location-alert {
  margin: 200px auto;
  max-width: 400px;
  opacity: 1;

  md-dialog-content div.location-alert-icon {
    color: #f6af00;
    font-size: 35px;
    font-size: 3.5rem;
    text-align: center;

    span:before {
      background-color: #f6af00;
      border-radius: 100px;
      color: #fff;
      padding: 10px;
    }
  }
}

md-dialog md-dialog-content:not([layout="row"]) > *:first-child.location-alert-icon {
  margin: 10px 0;
}



