@media print {
  md-sidenav.peach-sidenav,
  md-toolbar.peach-toolbar,
  .peach-search,
  .peach-page-header .icon-x {
    display: none !important;
  }

  peach-card {
    md-card {
      margin: 30px 0 !important;
    }
  }

  peach-modal {
    md-content.peach-modal {
      .modal-body {
        padding: 0;
      }
    }
  }

  peach-sidenav.peach-modal-active {
    & + #content.sidenav-open [ng-view] {
      margin-left: 0 !important;

      &.peach-modal {
        margin-left: 0 !important;
      }
    }
  }

  html, body {
    overflow: initial;
    height: auto;
  }

  .peach-page-content-row {
    #content {
      min-height: 900px;
    }
  }
}



