peach-select {
  flex: 1;

  md-input-container.peach-select-input {
    justify-content: space-between;
    padding-bottom: 0;

    &:not(.md-input-invalid).md-input-focused {

      md-icon {
        color: $font-color;
      }
    }

    label {
      margin: 0;
    }

    &:hover {
      cursor: pointer;
    }

    &.md-no-float {

      label {
        cursor: pointer;
      }

      &.md-input-has-value {

        label {
          display: none;
        }
      }

      &:not(.md-input-has-value) {

        input.selection-label {
          margin-top: -28px;
        }
      }
    }

    &.search-enabled.multi-select {

      &.peach-multi-has-value {

        label {
          display: none;
        }

        .md-chips {
          padding-bottom: 0;
        }
      }

      &:not(.peach-multi-has-value) {

        .md-chips {
          height: 37px;
        }
      }

      label {
        left: 0;
        top: -6px;
      }

      .md-chip-input-container input {
        display: none;
      }
    }

    input.selection-label {
      color: $font-color;
      cursor: pointer;
      text-overflow: ellipsis;
      white-space: nowrap;
      overflow: hidden;
      padding-right: 15px;
    }

    .icon-caret {
      cursor: pointer;
      background-color: transparent;

      position: absolute;
      right: 0;
      bottom: 6px;
      display: block;
      text-align: end;

      margin: 0 4px;
      color: rgba(0,0,0,0.26);
      font-size: 10px;
      font-size: 1.0rem;
    }
  }
}



