$table-row-background: #FFFFFF;
$table-row-alt-background: #F4F4F4;
$table-pager-active-background: #4DB0F7;

table.ng-table {
  border-spacing: 0;

  thead {

    tr {

      th {
        padding: 16px 0px 16px 20px;
        text-align: left;
        font-size: 16px;
        font-size: 1.6rem;
        font-weight: 600;
      }
    }
  }

  tbody {

    tr {

      td {
        padding: 15px 0px 14px 20px;
        font-size: 16px;
        font-size: 1.6rem;
        font-weight: normal;

        a {
          transition: $font-color-hover-animation;
          color: $font-link-color;
          text-decoration: none;
          cursor: pointer;

          &:hover {
            color: $font-link-color-hover;
          }
        }

        md-switch {

          &:first-child {
            margin-top: 0;
          }

          &:last-child {
            margin-bottom: 0;
          }
        }


      }
    }
  }



  tr {
    background: $table-row-background;

    th, td {
      border-bottom: 1px solid $table-row-border;

      &:last-child {
        width: 100%;
        padding-right: 20px;
      }

      &.thumbnail-small {
        min-width: 45px;
        width: 45px;

        img {
          border-radius: 50%;
        }
      }
    }
  }
}

[ng-table-pagination] {
  padding: 0 20px;

  .ng-table-pager {

    ul.ng-table-pagination {
      list-style: none;
      padding-left: 0;
      justify-content: center;
      align-items: center;
      margin: 0;

      li {
        display: inline-block;

        &:nth-child(even) {
          background: $table-row-alt-background;
        }

        &.active {
          background: $table-pager-active-background;

          a {
            color: $inverse-font-color;
          }
        }

        a {
          font-size: 13px;
          font-size: 1.3rem;
          text-decoration: none;
          display: block;
          padding: 5px 10px;
          text-align: center;
          min-width: 10px;
          color: $nav-link-color;
        }
      }
    }
  }
}



