md-sidenav.peach-sidenav {
  background-color: $sidenav-background;

  &.md-locked-open,
  &.md-locked-open-remove.md-closed,
  &.md-locked-open.md-closed,
  &.md-locked-open.md-closed.md-sidenav-left,
  &.md-locked-open.md-closed.md-sidenav-right {
    width: 180px;
    max-width: 180px;
    min-width: 180px;
    overflow: hidden;
  }

  .tablet-wrapper {
    overflow: hidden;
  }

  md-progress-linear {
    margin-top: -1px;
    height: 1px;
    z-index: 2;

    .md-container {
      background-color: $sidenav-loading-linear-background;
      height: 1px;

      .md-bar {
        height: 1px;
      }
    }
  }

  md-toolbar {
    background-color: $sidenav-background;
    height: 50px;
    min-height: 50px;
    max-height: 50px;

    &:first-child {
      border-bottom: 1px solid $sidenav-title-border-color;

      h3 {
        color: $sidenav-title-color;
        font-weight: 100;
        font-size: 20px;
        font-size: 2.0rem;
        margin: 0 10px;
        height: 50px;
        line-height: 50px;
        text-overflow: ellipsis;
        white-space: nowrap;
        overflow: hidden;
        opacity: 0;
        text-transform: uppercase;
      }

      .branding {

        img {
          opacity: 0;
        }

        span {
          opacity: 0;
        }
      }
    }

    &:last-child {
      background-color: $sidenav-location-picker-background;
      align-items: center;

      span.icon-location {
        margin: 0 0 0 5px;
        font-size: 24px;
        font-size: 2.4rem;
      }

      peach-select {
        opacity: 0;

        input.selection-label {
          color: $sidenav-inactive-item-color;
          font-size: 16px;
          font-size: 1.6rem;
          font-weight: 100;
        }

        .icon-caret {
          display: none;
        }
      }
    }
  }

  md-content {
    background-color: $sidenav-background;
    opacity: 0;

    md-list {
      padding: 10px 0;

      .md-subheader {
        color: $sidenav-inactive-item-color;
        background: transparent;
        font-size: 12px;
        font-size: 1.2rem;
        font-weight: 100;
        margin: 60px 0 0 10px;
        text-transform: uppercase;

        .md-subheader-inner {
          padding-left: 0;
        }
      }

      md-list-item {
        flex: initial;
        align-items: center;
        padding: 0;
        min-height: 42px;
        max-height: 42px;

        &.active,
        &:hover {

          .md-list-page-link {

            a {
              color: $sidenav-active-item-color;
            }
          }
        }

        .md-list-page-link {
          width: 100%;

          a {
            color: $sidenav-inactive-item-color;
            text-decoration: none;
            display: block;
            padding: 10px;

            &:hover {
              text-decoration: none;
            }
          }
        }
      }
    }

    peach-accordion {

      peach-accordion-header {

        div {
          padding: 8px 20px;

          h4 {
            margin: 0;
            cursor: pointer;
            color: $mobile-sidenav-header-color;

            a {
              color: inherit;
              text-decoration: none;
            }
          }
        }
      }

      peach-accordion-body  {

        div {
          background-color: $mobile-sidenav-accordion-background;

          md-divider {
            border-top-color: $mobile-sidenav-divider-color;
            margin-top: 3px;
          }

          h2 {
            color: $inverse-font-color;
            font-size: 13px;
            font-size: 1.3rem;
            font-weight: 100;
            margin: 0 20px;
            text-transform: uppercase;
          }

          md-list {
            padding: 2px 0;

            md-list-item {
              min-height: 43px;

              a {
                color: $mobile-sidenav-link-font-color;
                cursor: pointer;
                font-weight: 400;
                text-decoration: none;
              }

              &.active a {
                color: $mobile-sidenav-active-link-font-color;
                font-weight: 600;
              }

              .md-list-item-inner {
                background-color: transparent;
                z-index: 2;
                position: relative;
              }

              button.md-button {
                padding: 0 36px;

                &:not([disabled]):hover {
                  background-color: transparent;

                  a {
                    color: $mobile-sidenav-active-link-font-color;
                  }
                }
              }
            }

            .md-subheader {
              margin-top: 0;
              margin-left: 35px;
            }
          }
        }
      }

      .peach-static-panel {
        padding: 0 4px;

        h4 {
          margin: 0;
          font-weight: 600;
          padding-left: 13px;
          display: inline-block;

          a {
            color: $mobile-sidenav-link-font-color;
            text-decoration: none;
          }
        }

        md-list {
          padding: 0;
        }
      }
    }
  }

  /* Mobile */
  @media (max-width: 767px) {
    z-index: 81;
    transition: margin-top 0.33s;
    transition-timing-function: ease-out;
    background-color: $mobile-sidenav-background;
    color: $mobile-sidenav-header-color;
    font-size: 16px;
    font-size: 1.6rem;
    font-weight: 600;

    md-toolbar {
      background: $mobile-sidenav-background;
      padding: 20px 20px 0;
      height: 85px;
      min-height: 85px;
      max-height: 85px;

      &:first-child {
        border-bottom: 0;
      }

      img {
        height: 43px;
      }

      peach-select {
        padding-top: 5px;
        opacity: 0;

        md-input-container.peach-select-input {

          input.selection-label,
          span.icon-caret {
            border-bottom: 0;
            color: $mobile-sidenav-link-font-color;
          }
        }
      }
    }

    md-content {
      background-color: $mobile-sidenav-background;
      overflow: hidden;

      .icon {
        width: 32px;
        height: 32px;
        background-size: 32px 32px;
        background-color: $mobile-sidenav-location-background;
        display: inline-block;
        font-size: 30px;
        font-size: 3rem;
        vertical-align: middle;

        &.core-icon {
          margin-top: 4px;
          text-align: center;
          color: $mobile-sidenav-header-color;
        }
      }

      .peach-static-panel {

        .icon {

          &.core-icon {
            margin-left: 15px;
          }
        }
      }
    }
  }
}



