peach-card-content {

  & > [ng-transclude] {
    display: flex;
    flex-direction: column;

    &.can-load {
      opacity: 0;
    }

    .md-padding {
      padding: 20px;
    }

    .layout-padding {
      padding: 20px;
    }

    & > md-chips {
      .md-chips {
        box-shadow: none;
        margin: 20px 20px 0;
        padding: 0;

        .md-chip {
          margin: 0 20px 20px 0;
        }
      }
    }

    peach-message {

      .message-wrapper {
        margin: 0;
      }
    }

    @media (max-width: 768px) {
      md-input-container.md-input-has-value {

        label {
          transform: translate3d(0,10px,0) scale(.75);
        }
      }
    }
  }
}



