.peach-notice {

  md-toolbar {
    background-color: $font-link-color;
    min-height: 48px;

    .md-toolbar-tools {
      font-size: 16px;
      font-size: 1.6rem;
      height: 48px;
      justify-content: center;
      text-align: center;

      .icon {
        margin-right: 10px;
      }

      .md-button {
        margin-left: 20px;
        padding-top: 2px;
      }
    }
  }

  md-content {
    max-height: 300px;
    -webkit-transition: max-height 0.45s ease-in;
    transition: max-height 0.45s ease-in;


    &.ng-hide {
      max-height: 0;
      -webkit-transition: max-height 0.45s ease-out;
      transition: max-height 0.45s ease-out;
    }
  }

  md-list-item {
    justify-content: center;
  }

  a {
    cursor: pointer;
  }
}



